






















































export default {
	props: {
		//颜色
		color: {
			type: String,
			default: '#409eff'
		},
		//是否显示秒 针对type为datetime或time时生效
		showSeconds: {
			type: Boolean,
			default: false
		},
		//初始的值
		value: [String, Array, Object]
		,
		//类型date range datetime time
		type: {
			type: String,
			default: 'range'
		},
		//是否显示
		show: {
			type: Boolean,
			default: false
		},
		//初始格式
		format: {
			type: String,
			default: ''
		},
		//显示公历节日
		showHoliday: {
			type: Boolean,
			default: true
		}
	},
	data() {
		return {
			date: new Date(),
			weeks: ["一","二","三","四","五","六","日"],
			days: [],
			hackComputed: 0,
			checkedDateList: [],
			holidays: {
				'0101': '元旦',
				'0214': '情人',
				'0308': '妇女',
				'0312': '植树',
				'0401': '愚人',
				'0501': '劳动',
				'0504': '青年',
				'0601': '儿童',
				'0701': '建党',
				'0801': '建军',
				'0903': '抗日',
				'0910': '教师',
				'1001': '国庆',
				'1031': '万圣',
				'1224': '平安',
				'1225': '圣诞'
			},
			showTimePicker: false,//是否显示时间选择器
			timeValue: [0, 0, 0],//时间选择器的值
			time: [0, 0, 0]//选择后的时间
		};
	},
	created(){
		this.refreshDays();
	},
	methods: {
		//转为标准日期格式
		parseDate(str){
			return new Date(str.replace('年','/').replace('月','/').replace('日','').replace(/-/g,'/'));
		},
		//格式化日期
		formatDate(date, fmt) {
			var o = {
				"m+": date.getMonth() + 1, //月份   
				"d+": date.getDate(), //日   
				"h+": date.getHours(), //小时   
				"i+": date.getMinutes(), //分   
				"s+": date.getSeconds(), //秒   
				"q+": Math.floor((date.getMonth() + 3) / 3), //季度
			};
			if (/(y+)/.test(fmt))
				fmt = fmt.replace(RegExp.$1, (date.getFullYear() + "").substr(4 - RegExp.$1.length));
			for (var k in o)
				if (new RegExp("(" + k + ")").test(fmt))
					fmt = fmt.replace(RegExp.$1, (RegExp.$1.length == 1) ? (o[k]) : (("00" + o[k]).substr(("" + o[k]).length)));
			return fmt;
		},
		//刷新日期
		refreshDays() {
			let it = new Date(this.date);
			it.setDate(1);
			let oneWeek = it.getDay() == 0 ? 7 : it.getDay(); //这月1号是周几
			it.setDate(it.getDate() - (oneWeek - 1));
			this.days = [];
			for (let i = 0; i < 42; i++) {
				let day = {
					date: new Date(it),
					text: it.getDate(),
					range: 'none',//none 正常 begin 开始 end 结束
					bgStyle: {},//背景样式
					pointerStyle: {},//焦点样式
					flagStyle: {}//标志样式
				};
				//选中了
				if(this.checkedDateList.find((value)=> +value == +(day.date) )){
					day.pointerStyle.background = this.color;
					day.pointerStyle.color = "#fff";
				} else {
					let now = new Date();
					//今日
					if(day.date.getMonth() == now.getMonth() && day.date.getFullYear() == now.getFullYear() && day.date.getDate() == now.getDate()) {
						day.flagStyle.background = this.color;
					}
					//非本月的日期
					if(it.getMonth() < this.date.getMonth() || it.getMonth() > this.date.getMonth()) {
						day.pointerStyle.color = "#bbb";
					} else if(this.showHoliday) {
						//公历节日
						let holiday = this.formatDate(day.date, 'mmdd');
						if(this.holidays[holiday]) {
							day.text = this.holidays[holiday];
							day.flagStyle.background = this.color;
						}
					}
				}
				//范围选择
				if(this.checkedDateList.length==2) {
					let cur = +day.date, min = +this.checkedDateList[0], max = +this.checkedDateList[1];
					if(cur>=min && cur<=max) {
						day.bgStyle.background = this.color;
						if(cur==min) {
							day.range = 'begin';
						} else if(cur==max) {
							day.range = 'end';
						}
					}
				}
				this.days.push(day);
				it.setDate(it.getDate() + 1);
			}
		},
		//选择了日期
		onCheckedDay(day, index) {
			let checkedLength = this.checkedDateList.length;
			if((this.type=='range' && checkedLength==2) || (this.type!='range' && checkedLength)) this.checkedDateList = [];
			this.checkedDateList.push(day.date);
			this.checkedDateList.sort((a,b) => a - b);
			this.refreshDays();
		},
		//改变年份
		onSetYear(value) {
			this.date.setFullYear(this.date.getFullYear() + value);
			this.refreshDays();
			this.refreshComputed();
		},
		//改变月份
		onSetMonth(value) {
			this.date.setMonth(this.date.getMonth() + value);
			this.refreshDays();
			this.refreshComputed();
		},
		//设置时间选择器显示状态
		onSetTimePicker(show) {
			this.showTimePicker = show;
		},
		//确定时间选择
		onConfirmTime() {
			this.time = this.timeValue;
			this.onSetTimePicker(false);
		},
		//时间选择变更
		onTimeChange(e) {
			this.timeValue = e.detail.value;
		},
		//取消
		onCancel() {
			this.$emit('cancel', false);
		},
		//确定
		onConfirm() {
			let values = [];
			let defaultFormat = {
				'date': 'yyyy/mm/dd',
				'datetime': 'yyyy/mm/dd hh:ii' + (this.showSeconds?':ss':''),
				'time': 'hh:ii' + (this.showSeconds?':ss':''),
				'range': 'yyyy/mm/dd'
			};
			let errorMsg = {
				'date': '请选择日期',
				'range': '至少选择两个日期',
				'datetime': '请选择日期'
			};
			let checkedLength = this.checkedDateList.length;
			if((!checkedLength || (this.type=='range'&&checkedLength<2)) && this.type!='time'){
				return uni.showToast({icon:'none',title: errorMsg[this.type]});
			}
			//获取格式
			let format = this.format ? this.format : defaultFormat[this.type];
			this.checkedDateList.forEach(date=>{
				let temp = new Date(date);
				temp.setHours(this.time[0]), temp.setMinutes(this.time[1]);
				if(this.showSeconds) temp.setSeconds(this.time[2]);
				values.push(this.formatDate(temp,format));
			});
			let result = null;
			if(this.type!='time'){
				result = this.type=='range' ? values : values[0];
			}else{
				let date = new Date();
				date.setHours(this.time[0]), date.setMinutes(this.time[1]);
				if(this.showSeconds) date.setSeconds(this.time[2]);
				result = this.formatDate(date, format);
			}
			this.$emit('confirm', {value: result});
		},
		//强制更新computed
		refreshComputed(){
			this.hackComputed = +(new Date());
		}
	},
	computed: {
		Year() {
			this.hackComputed; //触发依赖更新
			return this.date.getFullYear();
		},
		Month() {
			this.hackComputed; //触发依赖更新
			return ('0' + (this.date.getMonth() + 1)).slice(-2);
		}
	},
	watch: {
		show(newValue, oldValue) {
			this.isShow = newValue;
		}
	}
}
